.timeslot-item {
  color: #ffffff;
  position: absolute; }
  .timeslot-item .timeslot-item-inner {
    border-right: 1px solid #3e5b76;
    border-bottom: 1px solid #3e5b76;
    height: 45px;
    padding: 15px 30px; }
    .timeslot-item .timeslot-item-inner:hover {
      background: #3e5b76;
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
      cursor: pointer;
      transition: .5s all ease; }
